.header__link {
    margin: 0;
    padding: 0;
    display: block;
    color: white;
    font-size: 18px;
    line-height: 22px;
    text-decoration: none;
    cursor: pointer;
}

@media screen and (max-width: 767px) {
    .header__link {
        font-size: 14px;
        line-height: 17px;
    }
}